.codex-loader .linespinner {
  width: 10px;
  height: 30px;
  background: #1a7cbc;
  position: relative;
  -webkit-animation: linespinner 1s ease-in-out infinite;
  animation: linespinner 1s ease-in-out infinite;
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
  border-radius: 8px;
}
.codex-loader .linespinner:after,
.codex-loader .linespinner:before {
  border-radius: 8px;
  content: "";
  position: absolute;
  width: 10px;
  height: 30px;
  background: #1a7cbc;
  -webkit-animation: linespinner 1s ease-in-out infinite;
  animation: linespinner 1s ease-in-out infinite;
}
.codex-loader .linespinner:before {
  right: 18px;
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
}
