  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  border-radius: 3px;
  overflow: hidden;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch input:checked + .switch-btn::before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}
.switch input:disabled + .switch-btn {
  opacity: 0.5;
}
.switch .switch-btn {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.switch .switch-btn:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 3px;
}
