.timeline {
  position: relative;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.timeline:after {
  content: "";
  position: absolute;
  width: 6px;
  background-color: #f4f6fb;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}
