  left: -10px;
  border-width: 10px 10px 10px 0;
  border-color: rgba(0, 0, 0, 0) #f4f6fb rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
}
.timeline-grid {
  padding-top: 40px;
  position: relative;
}
.timeline-grid .icon-wrap {
  position: absolute;
  top: 45px;
  left: calc(50% - 25px);
  width: 50px;
  height: 50px;
  background-color: #f4f6fb;
  border: 4px solid #f07521;
  border-radius: 50%;
  z-index: 1;
  overflow: hidden;
}
.timeline-grid .icon-wrap img {
  width: 100%;
  height: 100%;
}
.timeline-grid .timeline-content {
  padding: 30px;
  background-color: #f4f6fb;
  position: relative;
  border-radius: 6px;
  width: 45%;
}
.timeline-grid .timeline-content::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  border: medium solid #f4f6fb;
}
.timeline-grid .timeline-content h3 {
  margin-bottom: 5px;
  font-size: calc(20px + 4 * (100vw - 420px) / 1500);
