  right: -10px;
  border-width: 10px 0 10px 10px;
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #f4f6fb;
}
.timeline-grid.right .timeline-content {
  margin-left: auto;
}
.timeline-grid.right .timeline-content:before {
  left: -10px;
  border-width: 10px 10px 10px 0;
  border-color: rgba(0, 0, 0, 0) #f4f6fb rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
}
