    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .menu-list.open {
    right: 0;
    opacity: 1;
    visibility: visible;
    border-top: 1px solid #e5e5e5;
  }
  .menu-list li {
    display: block;
    position: relative;
  }
  .menu-list li .close-menu {
    padding: 25px 20px;
  }
  .menu-list li .close-menu .menu-brand {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%;
  }
  .menu-list li .close-menu .menu-brand img {
    width: 130px;
    height: auto;
  }
  .menu-list li .close-menu .menu-brand svg {
    width: 30px;
    height: 30px;
  }
  .menu-list li + li {
    border-top: 1px solid #e5e5e5;
  }
  .menu-list li a {
    padding: 10px 20px;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-size: 16px;
  }
  .menu-list li .submenu-list {
    border-top: 1px solid #e5e5e5;
  }
  .menu-list li .submenu-list a {
    -webkit-padding-start: 30px;
    padding-inline-start: 30px;
    color: #8392a5;
    font-size: 14px;
  }
  .menu-list li .submenu-list a i {
    position: absolute;
    top: 15px;
    right: 20px;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    font-weight: 600;
    font-size: 16px;
  }
  .menu-list li .submenu-list li .secodnmenu-list {
    border-top: 1px solid #e5e5e5;
  }
  .menu-list li .submenu-list li .secodnmenu-list a {
    -webkit-padding-start: 45px;
    padding-inline-start: 45px;
  }
  .menu-list > li > a i {
    -webkit-margin-start: auto;
    margin-inline-start: auto;
  }
}
